
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'

export default {
data () {
return {
timeSelected: [1, 2],
times: [{
id: 1,
title: '10秒',
price: '10'
}, {
id: 2,
title: '30秒',
price: '15'
}, {
id: 3,
title: '60秒',
price: '20'
}]
}
},
mounted () {

},
methods: {
onClick (itemValue, itemDisabled) {
console.log(itemValue)
console.log(this.timeSelected)
},
change (event) {
console.log(event)
},
edit (index) {
let _this = this
this.$vux.confirm.prompt('价格', {
title: '请输入价格',
onShow () {
console.log('promt show')
_this.$vux.confirm.setInputValue(_this.times[index].price)
},
onHide () {
console.log('prompt hide')
},
onCancel () {
console.log('prompt cancel')
},
onConfirm (msg) {
alert(msg)
}
})
}
},
components: {
Checker,
CheckerItem,
CheckIcon
}
}



