
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'

export default {
data () {
return {
themeSelected: [1, 2],
themes: [{
id: 1,
title: '生日霸屏',
price: '10'
}, {
id: 2,
title: '求约霸屏',
price: '15'
}, {
id: 3,
title: '示爱霸屏',
price: '20'
}]
}
},
mounted () {
console.log('111')
},
methods: {
onClick (itemValue, itemDisabled) {
console.log(itemValue)
console.log(this.themeSelected)
},
change (event) {
console.log(event)
},
edit (index) {
let _this = this
this.$vux.confirm.prompt('价格', {
title: '请输入价格',
onShow () {
console.log('promt show')
_this.$vux.confirm.setInputValue(_this.themes[index].price)
},
onHide () {
console.log('prompt hide')
},
onCancel () {
console.log('prompt cancel')
},
onConfirm (msg) {
alert(msg)
}
})
}
},
components: {
Checker,
CheckerItem,
CheckIcon
}
}



